* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.container {
  min-width: 380px;
  justify-content: center;
  margin: 0 auto;
  padding-bottom: 0;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
}

body {
  background: url("map_for_background_2.f7867647.jpg") center / cover no-repeat fixed;
  margin-top: 30px;
  font: 14px / 20px Lato, Arial, sans-serif;
}

.inputbox {
  width: 196px;
  margin-top: 20px;
  position: relative;
}

.inputbox input {
  width: 100%;
  box-shadow: none;
  color: #010102;
  letter-spacing: .05em;
  z-index: 10;
  background: none;
  border: none;
  outline: none;
  padding: 20px 10px 10px;
  font-size: 1em;
  transition: all .5s;
  position: relative;
}

.inputbox span {
  color: #fff;
  letter-spacing: .05em;
  pointer-events: none;
  padding: 20px 10px 10px;
  font-size: 1em;
  transition: all .5s;
  position: absolute;
  left: 0;
}

.inputbox input:valid ~ span, .inputbox input:focus ~ span {
  color: #e9f1f8;
  font-size: 0, 75em;
  margin-top: -10px;
  transform: translateX(-10px)translateY(-34px);
}

.inputbox i {
  width: 100%;
  height: 2px;
  pointer-events: none;
  z-index: 9;
  background: rgba(216, 217, 235, .718);
  border-radius: 4px;
  transition: all .5s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.inputbox input:valid ~ i, .inputbox input:focus ~ i {
  height: 44px;
}

.country-info {
  min-width: 300px;
  background-color: rgba(233, 241, 248, .635);
  justify-content: flex-start;
  margin-top: 40px;
  display: flex;
}

.country-box-list {
  padding-left: 10px;
  list-style: none;
}

.country-box-list li {
  letter-spacing: .03em;
  color: #212121;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 19px;
}

.country-box-name {
  text-align: center;
  margin: 0;
}

.coutry-box-span {
  margin-right: 5px;
  font-weight: 700;
}

.country-box-top {
  align-items: center;
  gap: 20px;
  display: flex;
}

.country-list {
  padding-left: 0;
  list-style: none;
}

.country-list img {
  width: 40px;
}

.country-list li {
  background-color: rgba(233, 241, 248, .39);
  padding: 10px;
  font-size: 12px;
  font-weight: 700;
}

